/*------------------------------------------------------------------
  [Switch button]
*/
.switch-button {
  display: inline-block;
  border-radius: 50px;
  background-color: #b3b3b3;
  width: 60px;
  height: 27px;
  padding: 2px;
  position: relative;
  overflow: hidden;
  vertical-align: middle;
}
.switch-button input[type="checkbox"] {
  display: none;
}
.switch-button label {
  border-radius: 50%;
  border: 1px solid #ababab;
  background-color: #ffffff;
  margin: 0;
  height: 23px;
  width: 23px;
  z-index: 1;
  display: inline-block;
  cursor: pointer;
  background-clip: padding-box;
}
.switch-button label:before {
  position: absolute;
  font-size: 11px;
  font-weight: 600;
  z-index: 0;
  content: "OFF";
  right: 0;
  display: block;
  width: 100%;
  height: 100%;
  line-height: 26px;
  top: 0;
  text-align: right;
  padding-right: 10px;
  color: #ffffff;
}
.switch-button input[type="checkbox"]:checked + span {
  background-color: #015291;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 2px;
}
.switch-button input[type="checkbox"]:checked + span label {
  float: right;
  border-color: #014478;
}
.switch-button input[type="checkbox"]:checked + span label:before {
  position: absolute;
  font-size: 11px;
  font-weight: 600;
  z-index: 0;
  content: "ON";
  color: #ffffff;
  left: 0;
  text-align: left;
  padding-left: 10px;
}
.switch-button.switch-button-yesno label:before {
  content: "NO";
}
.switch-button.switch-button-yesno input[type="checkbox"]:checked + span label:before {
  content: "YES";
}
.switch-button.switch-button-xs {
  height: 20px;
  width: 53px;
  line-height: 22px;
  width: 50px;
}
.switch-button.switch-button-xs label {
  height: 16px;
  width: 16px;
}
.switch-button.switch-button-xs label:before {
  line-height: 19px;
}
.switch-button.switch-button-sm {
  height: 24px;
  width: 57px;
  line-height: 26px;
}
.switch-button.switch-button-sm label {
  height: 20px;
  width: 20px;
}
.switch-button.switch-button-sm label:before {
  line-height: 23px;
}
.switch-button.switch-button-lg {
  height: 30px;
  width: 63px;
  line-height: 32px;
  width: 65px;
}
.switch-button.switch-button-lg label {
  height: 26px;
  width: 26px;
}
.switch-button.switch-button-lg label:before {
  line-height: 29px;
}
.switch-button.switch-button-success {
  background-color: #b3b3b3;
}
.switch-button.switch-button-success label {
  border-color: #ababab;
}
.switch-button.switch-button-success input[type="checkbox"]:checked + span {
  background-color: #83b97d;
}
.switch-button.switch-button-success input[type="checkbox"]:checked + span label {
  border-color: #73b06c;
}
.switch-button.switch-button-info {
  background-color: #b3b3b3;
}
.switch-button.switch-button-info label {
  border-color: #ababab;
}
.switch-button.switch-button-info input[type="checkbox"]:checked + span {
  background-color: #005291;
}
.switch-button.switch-button-info input[type="checkbox"]:checked + span label {
  border-color: #004478;
}
.switch-button.switch-button-warning {
  background-color: #b3b3b3;
}
.switch-button.switch-button-warning label {
  border-color: #ababab;
}
.switch-button.switch-button-warning input[type="checkbox"]:checked + span {
  background-color: #ffa050;
}
.switch-button.switch-button-warning input[type="checkbox"]:checked + span label {
  border-color: #ff9236;
}
.switch-button.switch-button-danger {
