/*Slider*/
.slider.slider-vertical {
  margin-right: 13px;
}
.slider-selection {
  background: #015291;
  border: 1px solid #015291;
  box-shadow: none;
}
.slider-track {
  background: #f2f2f2;
  box-shadow: none;
}
.slider-handle {
  opacity: 1;
  background: #FFFFFF;
  border: 3px solid #015291;
  background-clip: padding-box;
  box-shadow: none;
  height: 23px;
  width: 23px;
  transition: box-shadow 200ms ease-in-out;
}
.slider.slider-horizontal .slider-handle {
  margin-top: -7px;
}
.slider.slider-vertical .slider-handle {
  margin-left: -6px;
}
